const defineSmartGetter = (
  obj: Record<string, any>,
  key: string,
  valueFn: (...args: any[]) => any
): Record<string, any> => {
  Object.defineProperty(obj, key, {
    get: () => {
      delete obj[key];
      obj[key] = valueFn();
      return obj[key];
    },
    enumerable: true,
    configurable: true
  });

  return obj;
};

export default defineSmartGetter;
